html {
  --text-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.container {
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  width: 1024px;
  height: 60px;
  display: flex;
  position: relative;
}

.logo {
  width: 40px;
  height: 40px;
  position: absolute;
  left: 50px;
}

.nav {
  justify-content: center;
  align-items: center;
  display: inline-block;
  position: absolute;
  right: 50px;
}

.ul__nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.li__nav {
  margin-right: 20px;
  display: flex;
  position: relative;
}

.nav__link {
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.li__nav:last-of-type {
  margin: 0;
}

.nav__link:link, .nav__link:visited {
  color: #000;
}

.nav__link:hover {
  color: var(--text-color);
}

.nav__link:active {
  color: #00f;
}

.nav__link[data-qa="hover"]:hover {
  color: var(--text-color);
}

.nav__link.is-active {
  color: var(--text-color);
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 25%;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

@media (width: 1200px) {
  .container {
    width: 1200px;
  }
}
